import Vue from 'vue'
import Router from 'vue-router'
import Quote from '../pages/Quote'
import QuoteList from '../pages/QuoteList.vue'
import Enquiry from '../pages/Enquiry.vue'
import EnquiryList from '../pages/EnquiryList.vue'
import EnquiryDetail from '../pages/EnquiryDetail.vue'
import Create from '../pages/Create.vue'
import Print from '../pages/Print.vue'
import Login from "../pages/Login.vue"
import Body from "../pages/Body.vue"
import Register from "../pages/Register.vue"
Vue.use(Router)
const router= new Router({
  routes: [
    {
      path: '/',
      name: 'body',
      component: Body
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/body',
      name: 'Body',
      component: Body,
      redirect:"/quote",
      children: [
        {
          path: '/enquiry',
          name: "enquiry",
          component: Enquiry
        },
        {
          path: '/enquirydetail',
          name: "enquirydetail",
          component: EnquiryDetail
        },
        {
          path: '/enquiryList',
          name: "enquiryList",
          component: EnquiryList
        },
        {
          path: '/quote',
          name: "quote",
          component: Quote
        },
        {
          path: '/create',
          name: "create",
          component: Create
        },
        {
          path: '/quotelist',
          name: "quotelist",
          component: QuoteList
        },
        {
          path: '/print',
          name: "print",
          component: Print
        },
      ]
    }
  ]
})
//路由守卫
router.beforeEach((to,from,next)=>{
  if (to.path==="/login") return next();
  if (to.path==="/register") return next();
  const access_token=window.sessionStorage.getItem('access_token')
  if(!access_token) return next('/login')
  next()
})
export default router


