<template>
  <div class="quote">
    <h1>报价查询</h1>
    <el-form :model="quoteForm" ref="quoteForm" :rules="rules" label-position="left" label-width="">

      <el-form-item label="运输类型" prop="transportType" label-width="180px">
        <el-radio-group v-model="quoteForm.transportType">
          <el-radio value="海运整箱" label="海运整箱"></el-radio>
          <el-radio value="海运拼箱" label="海运拼箱"></el-radio>
          <el-radio value="空运" label="空运"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="进口/出口" prop="importOrOutport" label-width="180px">
        <el-radio-group v-model="quoteForm.importOrOutport">
          <el-radio value="进口" label="进口"></el-radio>
          <el-radio value="出口" label="出口"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="ORINGINAL 启运港" prop="Oringinalharbour" label-width="180px">
        <el-select v-model="quoteForm.Oringinalharbour" filterable placeholder="--港口--" style="width: 20rem;">
          <el-option v-for="(item,key) in original" :key="key" :label="item.name" :value="item.name">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="DESTINATION 目的地" prop="destinationharbour" label-width="180px">
        <el-select v-model="quoteForm.destinationharbour" filterable placeholder="--港口--" style="width: 20rem;">
          <el-option v-for="(item,key) in destination" :key="key" :label="item.name" :value="item.name">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="货型" prop="box" class="box-type" label-width="90px">
        <template class="airOrBoat" style="margin-left: 0px;">
          <el-table :data="airData" border style="width: 20rem" v-if="!isAir">
            <el-table-column prop="title" label="空运">
              <el-table-column prop="currency" label="WIGHT CLASS">
              </el-table-column>
              <el-table-column>
                <template slot-scope="scope">
                  <el-radio :label="scope.$index" v-model="radio" @change.native="getCurrentRow(scope.$index)"></el-radio>
                </template>
              </el-table-column>
            </el-table-column>
          </el-table>

          <el-table :data="tableData" class="tb-edit" border style="width: 20rem" v-if="!isBoat" highlight-current-row @row-click="handleCurrentChange">
            <el-table-column prop="title" label="海运整箱">
              <el-table-column prop="size" label="CNRE SIZE">
              </el-table-column>
              <el-table-column prop="quantity" label="QTY">
                <template slot-scope="scope">
                  <el-input size="small" v-model="scope.row.quantity" placeholder="请输入内容" @change="handleEdit(scope.$index, scope.row)"></el-input>
                  <span>{{scope.row.quantity}}</span>
                </template>
              </el-table-column>
              <el-table-column type="selection" width="55" @click="handleEdit(scope.$index, scope.row)">
              </el-table-column>
            </el-table-column>
          </el-table>

          <el-table :data="SpliceData" border style="width: 20rem" v-if="!isSplice">
            <el-table-column prop="title" label="海运拼箱">
              <el-table-column prop="size" label="CNRE SIZE">
              </el-table-column>
              <el-table-column prop="quantity" label="QTY">
                <template slot-scope="scope">
                  <el-input size="small" v-model="scope.row.quantity" placeholder="请输入内容" @change="handleEdit(scope.$index, scope.row)"></el-input>
                  <span>{{scope.row.quantity}}</span>
                </template>
              </el-table-column>
              <el-table-column type="selection" width="55" @click="handleEdit(scope.$index, scope.row)">
              </el-table-column>
            </el-table-column>
          </el-table>
        </template>
      </el-form-item>
      <router-link to='quotelist'>
        <el-button type="info" @click="onSubmitQuoteForm(quotenForm)">查询</el-button>
      </router-link>
    </el-form>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'EnquityDetail',
    data() {
      return {
        //base_api_url:null,
        radio: '',
        airData: [{
            currency: '+45KGS',
          }, {
            currency: '+100KGS',
          },
          {
            currency: '+300KGS',
          },
          {
            currency: '+500KGS',
          },
          {
            currency: '+1000KGS',
          }

        ],
        tableData: [{
          size: '20GP',
          quantity: '1',
        }, {
          size: '40hq',
          quantity: '2',
        }],
        SpliceData: [{
          size: 'LCL',
          quantity: '',
        }],
        original: [{
          name: 'PVG'
        }],
        destination: [{
          name: 'NRT',
          name: 'NGO'
        }],
        quoteForm: {
          Oringinalharbour: '',
          destinationharbour: '',
          transportType: '',//判断运输方式
          importOrOutport: '',
          box: '',
          daterange: ''
        },
        rules: {
          Oringinalharbour: [{
            required: true,
            message: '--港口--'
          }],
          destinationharbour: [{
            required: true,
            message: '--港口--'
          }],
          importOrOutport: [{
            required: true,
            message: '请选择进出口'
          }],
          transportType: [{
            required: true,
            message: '请选择运输类型'
          }],
        }
      }
    },
    // mounted: async function() {
    //   let res = await axios.get(this.base_api_url + '/api/oringinal');
    //   let original = this.original
    //   let destination = this.destination
    //   let type_jinchukou  = this.importOrOutport
    //   let  type_traffic = this.transportType
    //   let box_pile = this.box
    //   axios.get(this.base_api_url + '/api/inquiryform', {
    //       params: {
    //         original,
    //         destination,
    //         type_jinchukou,
    //         type_traffic,

    //       }
    //     })
    //     .then((res) => {
    //       this.original = res.data.original,
    //       this.destination = res.data.destination,
    //       // this.importOrOutport = res.data.type_jinchukou,
    //       // this.searchForm.transportType = res.data.type_traffic,
    //       // this.searchForm.box = res.data.box_pile,
    //       this.$router.push({
    //           name: 'quotelist',
    //           query: {
    //             list: res.data
    //           }
    //         })
    //       this.$message({
    //         message: '提交成功',
    //         type: 'success'
    //       })
    //     })
    // },
    methods: {
      onSubmitQuoteForm(res){
        console.log(res)
      },
      getCurrentRow(index, row) { //获取选中数据
        this.getCurrentRow = row;
        console.log("row")
      },
      handleCurrentChange(row, event, column) {
        console.log(row, event, column, event.currentTarget)
      },
      handleEdit(index, row) {
        console.log(index, row);
      }
    },
    computed: {
      isAir: function() { //判断是否为空运
        if (this.quoteForm.transportType === "空运") {
          return false;
        }
        return true
      },
      isBoat: function() { //判断是否为海运整箱
        if (this.quoteForm.transportType === "海运整箱") {
          return false;
        }
        return true
      },
      isSplice: function() { //判断是否为海运拼箱
        if (this.quoteForm.transportType === "海运拼箱") {
          return false;
        }
        return true
      },
    }
  }
</script>

<style lang="scss">
  .box-type {
    display: flex;
  }

  .airOrBoat {
    display: flex;
  }

  .tb-edit .el-input {
    display: none
  }

  .tb-edit .current-row .el-input {
    display: block
  }

  .tb-edit .current-row .el-input+span {
    display: none
  }
</style>



