<template>
  <div class="enquity">
    <h1>门店成本查询</h1>
    <el-form :model="searchForm" ref="searchForm" :rules="rules" label-position="left" label-width="120px">
      <el-form-item label="国家COUNTRY" prop="country">
        <el-select v-model="searchForm.country" filterable placeholder="--请选择国家--" :change="modifyCity">
          <el-option v-for="(item,key) in countries" :key="key" :label="key" :value="key">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="城市CITY" prop="city">
        <el-select v-model="searchForm.city" filterable placeholder="--请选择城市--">
          <el-option v-for="item in cities" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="邮编ZIP" prop="zip">
        <el-input v-model="searchForm.zip" placeholder="请输入内容" class="table1-input" style="width: 13.25rem;"></el-input>
      </el-form-item>

      <el-form-item label="进口/出口" prop="importOrOutport">
        <el-radio-group v-model="searchForm.importOrOutport">
          <el-radio value="进口" label="进口"></el-radio>
          <el-radio value="出口" label="出口"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="运输类型" prop="transportType">
        <el-radio-group v-model="searchForm.transportType">
          <el-radio value="海运整箱" label="海运整箱"></el-radio>
          <el-radio value="海运拼箱" label="海运拼箱"></el-radio>
          <el-radio value="空运" label="空运"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="箱型" prop="box">
        <el-select v-model="searchForm.box" clearable placeholder="--请选择--" :disabled="!isSea">
          <el-option value="20">20</el-option>
          <el-option value="40">40</el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="时间段">
        <!-- <el-date-picker v-model="searchForm.daterange" type="daterange" range-separator="至" start-placeholder="开始日期"
          end-placeholder="结束日期" format="yyyy 年 MM 月 dd 日" value-format="yyyy-mm-dd">
        </el-date-picker> -->
        <el-date-picker
              v-model="searchForm.daterange"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd">
            </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmitSearchForm('searchForm')" @submit.native.prevent>查询</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'enquiry',
    data() {
      return {
        base_api_url: 'https://baojia.widesight.cn/backend/public',
        countries: [],
        cities: [],
        pickerOptions: {
          shortcuts: []
        },
        searchForm: {
          country: '',
          city: '',
          zip: '',
          transportType: '', //运输类型
          importOrOutport: '', //进出 或 出口
          box: '', //海运箱型箱型
          daterange: ''
        },
        rules: {
          country: [{
            required: true,
            message: '请选择国家'
          }],
          city: [{
            required: true,
            message: '请选择城市'
          }],
          importOrOutport: [{
            required: true,
            message: '请选择进出口'
          }],
          transportType: [{
            required: true,
            message: '请选择运输类型'
          }],
        }
      }
    },
    computed: {
      isSea: function() { //判断是否为海运整箱
        if (this.searchForm.transportType === "海运整箱") {
          return true
        }
        return false
      },
      modifyCity: function(cities) { //city 去重
        cities = this.cities;
        console.log(cities);
        let newArr = [];
        cities.forEach(item =>{
          if(newArr.indexOf(item) == -1){
            newArr.push(item);
          }
        });
        console.log(newArr)
        this.cities = newArr;
      }
    },
    mounted: async function() {
      let res = await axios.get(this.base_api_url + '/api/citys');
      let countries = {};
      let cities = {};
      //TODO:获取的res赋值给 this.countries
      //let countries = res.data;
      for (let item in res.data) {
        countries[res.data[item]["country"]] = [];
      }
      for (let item in res.data) {
        countries[res.data[item]["country"]].push(res.data[item]["city"]);
      }
      this.countries = countries;
    },
    methods: {
      getCityList(e) { //根据countries对象 获取city list
        this.searchForm.city = ''
        this.cities = this.countries[this.searchForm.country]
        // for(let item in this.countries ){
        //   if(this.countries[item].country === this.searchForm.country ){
        //     this.cities = this.countries[item].city
        //   }
        // }
      },
      onSubmitSearchForm(formName) {
        this.msg = ''
        console.log(formName)
        this.$refs[formName].validate((valid) => { //表单验证通过 同步方法
          if (valid) {
            try {
              //   let res = await axios.post('https://940b3659ddd2.ngrok.io/api/inquiryform',{//post保存表单
              //   country: this.searchForm.country, // 国家
              //   city: this.searchForm.city, // 城市
              //   zip: this.searchForm.zip, //邮编
              //   type_jinchukou: this.searchForm.importOrOutport, //进口出口
              //   type_traffic: this.searchForm.transportType, //运输类型
              //   box_pile: this.searchForm.box, //海运箱型
              //   time: this.searchForm.daterange, //查询日期范围
              // })
              // console.log(this.searchForm.transportType)
              let country = this.searchForm.country
              let city = this.searchForm.city
              let zip = this.searchForm.zip
              let type_jinchukou = this.searchForm.importOrOutport
              let type_traffic = this.searchForm.transportType
              let box_pile = this.searchForm.box
              let time = this.searchForm.daterange
              axios.get(this.base_api_url + '/api/inquiryform', {
                  params: {
                    country, // 国家
                    city, // 城市
                    zip, //邮编
                    type_jinchukou, //进口出口
                    type_traffic, //运输类型
                    box_pile, //海运箱型
                    time, //查询日期范围
                  }
                })
                .then((res) => {
                  this.searchForm.country = res.data.country,
                    this.searchForm.city = res.data.city,
                    this.searchForm.zip = res.data.zip,
                    this.searchForm.importOrOutport = res.data.type_jinchukou,
                    this.searchForm.transportType = res.data.type_traffic,
                    this.searchForm.box = res.data.box_pile,
                    this.searchForm.daterange = res.data.time,
                    this.$router.push({
                      name: 'enquiryList',
                      query: {
                        list: res.data
                      }
                    })
                  this.$message({
                    message: '提交成功',
                    type: 'success'
                  })
                })
            } catch (e) {
              console.log(e); //捕捉 axios 请求错误
              this.$message.error('服务器错误，请稍后再试')
              // this.$router.push('/enquirylist')
            }
          } else { //表单验证错误
            console.log('error submit!!');
            return false;
          }
        });
      }
    },
    watch: {
      'searchForm.country': 'getCityList'
    }
  }
</script>

<style scoped>

</style>



