<template>
  <div class="body">
    <el-container>
      <el-aside>
        <el-menu
          default-active="2"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          :router="true"
        >
          <h5>
            <el-menu-item>
              <img class="brand" src="../assets/best-logo.jpg" alt="" />
            </el-menu-item>
          </h5>
          <el-menu-item index="quote" v-if="seen">
            <i class="el-icon-location"></i>
            <span slot="title"> 报价查询 </span>
          </el-menu-item>
          <el-menu-item index="enquiry">
            <i class="el-icon-menu"></i>
            <span slot="title"> 门店成本查询 </span>
          </el-menu-item>
          <el-menu-item index="create" v-if="seen">
            <i class="el-icon-document"></i>
            <span slot="title"> 报价单制作 </span>
          </el-menu-item>
          <el-menu-item index="4" v-if="seen">
            <i class="el-icon-setting"></i>
            <span slot="title">报价单号查询</span>
          </el-menu-item>
          <el-menu-item v-if="seen" @click="next">
            <i class="el-icon-truck"></i>
            <span slot="title">退出登录</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-main class="home-content">
        <router-view></router-view>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Body",
  data() {
    return {
      title: "COST RECORD ",
      seen: true,
    };
  },
  methods: {
    //退出登录
    next() {
      var that=this
      var access_token = window.sessionStorage.getItem("access_token");
      var id = window.sessionStorage.getItem("id");
      axios({
        method: "DELETE",
        headers: { Authorization: "Bearer " + access_token },
        url: "https://baojia.widesight.cn/backend/public/api/login/"+id,
      }).then(function (res1) {
        window.sessionStorage.clear()
        console.log(res1);
        that.$router.push("/login");
        that.$message({
          message:res1.data.info,
          type: "success",
        });
      });
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
  },
};
</script>

<style lang="scss" style="">
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  min-height: 100vh;
}

aside {
  /*height: 100%;*/
  h5 {
    text-align: center;
  }
}

.brand {
  max-height: 70px;
}

.el-header {
  /*background-color: rgba(84, 92, 100);*/
  display: flex;
  align-items: center;

  img {
    max-height: 40px;
  }
}

.el-header span {
  font-size: 24px;
}

body {
  margin: 0;
}

.el-menu {
  height: 100%;
}

.el-menu-item a {
  color: white;
}

.home-content {
  min-height: 100vh;
  padding-left: 5rem;
}

p {
  text-align: left;
}

.el-button--info {
  width: 200px;
}
</style>



