<template>
  <div class="register">
    <div class="register-item">
      <div>
        <img class="login-logo" src="../assets/best-logo.jpg" alt="" />
      </div>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="用户名:" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="邮箱:" prop="email">
          <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>
        <el-form-item label="密码:" prop="password">
          <el-input type="password" v-model="ruleForm.password"></el-input>
        </el-form-item>
        <el-form-item label="确认密码:" prop="repassword">
          <el-input type="password" v-model="ruleForm.repassword"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm(ruleForm)" :disabled="disabled"
            >立即注册</el-button
          >
          <el-button @click="resetForm()">返回</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "register",
  data() {
      var repassword = (rule, value, callback) => {
       if (value !== this.ruleForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      disabled:false,
      ruleForm: {
        name: "",
        email: "",
        password: "",
        repassword: "",
      },
      rules: {
        name: [
          { required: true, message: "请输入账号", trigger: "blur" },
          { min: 6, max: 10, message: "长度在 6 到 10 个字符", trigger: "blur" },
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { type: "email", message: "请输入正确得邮箱",trigger: "blur" },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
         { pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,10}$/, message: '您需要同时含有数字和字母，且长度要在6-10位之间',trigger: "blur" }
        ],
        repassword: [
          { required: true, message: "请确认密码",  trigger: "blur" },
          {validator: repassword, required: true, message: "两次密码不一致",trigger: "blur" },
          {
            min: 6,
            max: 10,
            message: "长度在 6 到 10 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
        var name=formName.name||null
        var email=formName.email||null
        var password=formName.password||null
        var c_password=formName.repassword||null
        var that=this
        if(name==null&&email==null&&password==null&&c_password==null){
            this.disabled=false
        }else{
            this.disabled=true
             axios({
              method: "POST",
              url: "https://baojia.widesight.cn/backend/public/api/register ",
              data:{
                  name,
                  email,
                  password,
                  c_password
              }
            })
            .then(function (res1) {
              that.$router.push("/login");
              that.$message({
                message: "注册成功",
                type: "success",
              });
            })
            .catch(function (err) {
            that.$message({
              showClose: true,
              message: "您的账号或邮箱貌似注册过了",
              type: "error",
            });
          });
        }
    },
    resetForm() {
       this.$router.push("/login");
    },
  },
};
</script> 

<style>
.register-item {
  width: 500px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 20%;
}
</style>


