<template>
  <div class="login">
    <div class="login-item">
      <div>
        <img class="login-logo" src="../assets/best-logo.jpg" alt="" />
      </div>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="用户名:" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="密码:" prop="password">
          <el-input type="password" v-model="ruleForm.password"></el-input>
        </el-form-item>
        <div class="login-button">
          <el-button
            type="primary"
            :loading="ruleForm.loading"
            @click="submitForm()"
            >{{ ruleForm.loadingName }}</el-button
          >
          <el-button @click="Register">注册</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Login",
  data() {
    return {
      ruleForm: {
        name: "",
        password: "",
        loading: false,
        loadingName: "登录",
      },
      rules: {
        name: [
          { required: true, message: "账号不能为空", trigger: "blur" },
          { min: 3, max: 6, message: "长度在 3 到 6 个字符", trigger: "blur" },
        ],
        password: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { min: 6, max: 10, message: "长度在 6 到 10 个字符", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    // 登录
    submitForm() {
      var username = this.ruleForm.name || null;
      var password = this.ruleForm.password || null;
      if (username == null && password == null) {
        this.$message({
          showClose: true,
          message: "请输入账号或密码",
          type: "error",
        });
      } else {
        this.ruleForm.loading = true;
        this.ruleForm.loadingName = "登陆中...";
        var that = this;
        axios({
          method: "POST",
          url: "https://baojia.widesight.cn/backend/public/api/login",
          data: {
            grant_type: "password",
            client_id: 2,
            client_secret: "kHzde7WWlET33KQ75kQwp5NwqJejSj813PRadUhA",
            username,
            password,
          },
        })
          .then(function (res) {
            console.log(res);
            window.sessionStorage.setItem("access_token",res.data.access_token)
            that.ruleForm.loading = false;
            that.ruleForm.loadingName = "登陆";
            //用户令牌
            axios({
              method: "GET",
              headers: { Authorization: "Bearer " + res.data.access_token },
              url: "https://baojia.widesight.cn/backend/public/api/user",
            }).then(function (res1) {
              console.log(res1)
              window.sessionStorage.setItem("id",res1.data.id)
              that.$router.push("/body");
              that.$message({
                message: "登录成功",
                type: "success",
              });
            });
          })
          .catch(function (err) {
            that.ruleForm.loading = false;
            that.ruleForm.loadingName = "登陆";
            that.$message({
              showClose: true,
              message: "账号或密码错误",
              type: "error",
            });
          });
      }
    },
    Register(){
      this.$router.push('/register')
    }
  },
};
</script>

<style scoped>
.login-logo {
  width: 500px;
  height: 120px;
}
.login-button {
  display: flex;
  justify-content: center;
}
.login-item {
  width: 500px;
  border: 1px solid #dcdfe6;
  border-radius: 20px;
  padding: 20px 20px 60px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 50%;
  margin-top: -240px;
}
</style>>



