<template>
  <div class="create" id="pdfCentent">
    <h1>报价单制作</h1>
    <div class="create-box">
      <div class="L">
        <div class="logo">
          <img src="../assets/best-logo.jpg" />
        </div>
        <div class="r">
          <p class="quo">QUOTATION NO.</p>
          <div class="L">
            <p class="pol">POL:</p>
            <input class="pol-input" />
          </div>
          <div class="L">
            <p class="pol">POD:</p>
            <input class="pol-input" />
          </div>
          <div class="L">
            <p class="quo">SERVICE MODE</p>
            <p class="quo">AIR/OCEAN</p>
          </div>
          <p>QUOTE DATE</p>
          <div class="L">
            <p class="quo">ACTIVE DATE</p>
            <input />
            <p class="quo">EXPIRE DATE</p>
            <input />
          </div>
          <div class="L">
            <p class="quo">PRINT DATE</p>
            <input />
          </div>
        </div>
      </div>
      <div class="L">
        <p class="cargo">CARGO DETAILS</p>
        <div>
          <p class="quo">TRANSIT TYPE</p>
          <p class="quo">DOOR TO DOOR</p>
        </div>
        <p class="quo">FREIGHT TERM</p>
      </div>

      <div class="L">
        <p class="width-a">EXTRA CHARGES</p>
        <p class="lh">RMB</p>
        <p class="lh">USD</p>
        <p class="lh">QTY</p>
        <p class="lh">TOTAL</p>
      </div>
      <div class="L">
        <ul class="itemlist">
          <li class="L">
            <p class="width-a">ITEM</p>
            <div class="border1"></div>
            <div class="border2"></div>
            <p class="width-a">1</p>
            <p class="width-a">2</p>
          </li>
        </ul>
      </div>
      <div class="L">
        <p>TOTAL</p>
        <p>USD</p>
        <p>RMB</p>
      </div>
      <div class="L">
        <p>自动合计</p>
        <p>自动合计</p>
      </div>
      <p>REMARK</p>
      <button type="info" @click="ExportSavePdf(htmlTitle,nowTime)">打印</button>
     <!-- <el-upload
  class="upload-demo"
  drag
   accept=".pdf" 
  :on-success="show"
  action="https://baojia.widesight.cn/backend/public/api/file/put"
  multiple>
  <i class="el-icon-upload"></i>
  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
  <div class="el-upload__tip" slot="tip">只能上传pdf文件，且不超过500kb</div>
</el-upload> -->
    </div>
  </div>
</template>

<script>
export default {
  name: "create",
  data() {
    return {
      htmlTitle:"PDF名称",
      nowTime:"",
      base_api_url: this.Common.base_api_url,
      new_date_json: {}, //数据结构
      multipleSelection: [], //复选框，数据
      is_edit: true, //是否可编辑
      is_delete: true, //是否可删除
      selection: true, //是否需要复选框
      radio: false, //单选变色
      space_color: true, //隔行变色
      //表头信息
      table_columns: [
        {
          prop: "carrier", //type of transport
          label: "CARRIER",
          width: "150",
        },
        {
          prop: "fee",
          label: "OCEAN FEE/AIR FREIGHT",
          width: "150px",
        },
        {
          prop: "box_class",
          label: "CONTAINER SIZE WIGHT CLASS",
          width: "200px",
        },
        {
          prop: "quantity",
          label: "QTY",
          width: "",
        },
        {
          prop: "total",
          label: "TOTAL",
          width: "",
        },
        {
          prop: "date",
          label: "ETD", //estimated time of departure 估计离港时间
          width: "",
        },
        {
          prop: "TRANSIT",
          label: "TRANSIT",
          width: "",
        },
        {
          prop: "currency",
          label: "币种",
          width: "",
        },
      ],
      //表格数据
      table_data: [],
    };
  },
  methods: {
    show(res){
      console.log(res)
    },
    handlePreview(res){
      console.log("res",res)
    },
    handleRemove(res){
      console.log(res)
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleClick(row) {
      console.log(row);
    },
    tableRowClassName() {
      //选取DOM节点
      var trs = this.$refs.mytable.$el
        .getElementsByTagName("tbody")[0]
        .getElementsByTagName("tr");
      for (var i in trs) {
        if (i % 2 == 0) {
          //当隔行变色未true时改变颜色
          if (this.space_color) {
            trs[i].style.backgroundColor = "#f0f9eb";
          } else {
            trs[i].style.backgroundColor = "";
          }
        }
      }
    },

    //多选框
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.log("selection:", this.multipleSelection);
    },
    //编辑
    handleEdit(index, row) {
      console.log(index, row);
      row.edit = true;
    },
    //删除
    handleDelete(index, row) {
      console.log(index, row);
      this.table_data.splice(index, 1);
      this.$message({
        message: "删除成功！",
        type: "success",
      });
    },
    //保存
    handleSave(index, row) {
      console.log(index, row);
      row.edit = false;
      delete this.table_data[index].add;
      this.$message({
        message: "保存成功！",
        type: "success",
      });
    },
    handleAdd() {
      var addDataJson = {};
      for (var key in this.new_date_json) {
        if (key === "edit") {
          delete addDataJson[key];
        } else if (key === "add") {
          delete addDataJson[key];
        } else {
          addDataJson[key] = "";
        }
      }
      addDataJson.edit = true;
      addDataJson.add = true;
      this.table_data.push(addDataJson);
    },
    //初始化编辑属性
    initEditAttribute() {
      var self = this;
      var edit = self.edit;
      var dataArray = [
        {
          carrier: "海运整箱",
          fee: "2495",
          box_class: "20GP",
          quantity: "18",
          total: "1111",
          date: "2020-12-03",
          TRANSIT: "2",
          currency: "CNY",
        },
        {
          carrier: "海运整箱",
          fee: "4858",
          box_class: "40GP",
          quantity: "18",
          total: "222",
          date: "2020-12-02",
          TRANSIT: "2",
          currency: "CNY",
        },
        {
          carrier: "海运整箱",
          fee: "4858",
          box_class: "40GP",
          quantity: "18",
          total: "444",
          date: "2020-12-04",
          TRANSIT: "2",
          currency: "CNY",
        },
      ];
      if (dataArray.length > 0) {
        //添加编辑事件
        for (var index in dataArray) {
          dataArray[index]["edit"] = false;
          this.table_data.push(dataArray[index]);
        }
        if (Object.keys(this.new_date_json).length === 0) {
          //新增时，初始化数据结构
          this.initAddDataJson(dataArray[0]);
        }
      }
      console.log("this.tableData:", this.table_data);
    },
    initAddDataJson(dataArray) {
      //新增时，初始化数据结构
      var dataJson = dataArray;
      var newDateJson = {};
      for (var key in dataJson) {
        if (key === "edit") {
          newDateJson[key] = "true";
        } else {
          newDateJson[key] = "";
        }
      }
      newDateJson["add"] = true;
      this.new_date_json = newDateJson;
    },
  },
  mounted: function () {
    this.initEditAttribute();
    //确保方法在页面渲染后调用
    this.$nextTick(function () {
      /////方法
      this.tableRowClassName();
    });
  },
  watch: {
    space_color: function () {
      //监听数据变化
      this.$nextTick(function () {
        /////方法
        this.tableRowClassName();
      });
    },
    table_data: function () {
      //监听数据变化f
      this.$nextTick(function () {
        /////方法
        this.tableRowClassName();
      });
    },
  },
};
</script>

<style>
.lh {
  line-height: 20px;
  margin: 0;
}

.create-box {
  width: 1200px;
  border: #000000 1px solid;
}

.logo {
  width: 322px;
  height: 100px;
  border-bottom: #000000 1px solid;
  border-right: #000000 1px solid;
  margin-right: 185px;
}

img {
  height: 88px;
}

.L {
  display: flex;
}

.r {
  padding-top: 20px;
}

.quo {
  width: 130px;
  height: 20px;
  margin: 0;
  line-height: 20px;
}

.pol {
  width: 130px;
  height: 30px;
  margin: 0;
  line-height: 30px;
}

.pol-input {
  width: 226px;
}

.cargo {
  width: 322px;
  height: 20px;
  margin: 0;
  line-height: 20px;
  margin-right: 185px;
}

.width-a {
  width: 240px;
  margin: 0;
  line-height: 20px;
}

.itemlist {
  padding-inline-start: 0px;
  list-style: none;
}

.border1 {
  border: #000000 1px solid;
  border-right: none;
  width: 40px;
  height: 20px;
}

.border2 {
  border: #000000 1px solid;
  width: 80px;
  height: 20px;
}

.tb-edit .input-box {
  display: none;
}

.tb-edit .current-cell .input-box {
  display: block;
  margin-left: -15px;
}
</style>



