<template>
  <div class="enquirydetail">
    <h1>详细页面</h1>
    <div class="table3">
      <p class="table3-name">国家COUNTRY</p>
      <input class="" v-model="detaildata.country">
    </div>
    <div class="table3">
      <p class="table3-name">城市CITY</p>
      <input class="" v-model="detaildata.city">
    </div>
    <div class="table3">
      <p class="table3-name">地址ADDRESS</p>
      <input class="address_type" v-model="detaildata.address">
    </div>
    <div class="table3">
      <p class="table3-name">数据DETAILS</p>
      <p class="data-unit">件</p>
      <input class="data-unit" v-model="detaildata.details_pieces">
      <p class="data-unit">毛</p>
      <input class="data-unit" v-model="detaildata.details_fur">
      <p class="data-unit">体</p>
      <input class="data-unit" v-model="detaildata.details_volume">
    </div>
    <div class="table3">
      <p class="table3-name">邮编ZIP</p>
      <input class="" v-model="detaildata.zip">
    </div>
    <div class="table3">
      <p class="table3-name">代理名字</p>
      <input class="" v-model="detaildata.agentname">
    </div>
    <div class="table3">
      <p class="table3-name">类型</p>
      <input class="table3-name" v-model="detaildata.type_traffic">
      <p class="table3-name">{{box}}</p>
    </div>
    <br>
    <div class="">
      <el-table :data="tableData" height="250" border style="width: 35rem" class="tb-edit" highlight-current-row
        @row-click="handleCurrentChange">
        <el-table-column prop="cost_breakdown" label="费用明细(英-中)" width="180">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.cost_breakdown" placeholder="请输入内容" @change="handleEdit(scope.$index, scope.row)"></el-input>
            <span>{{scope.row.cost_breakdown}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="currency" label="币种" width="180">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.currency" placeholder="请输入内容" @change="handleEdit(scope.$index, scope.row)"></el-input>
            <span>{{scope.row.currency}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="money" label="金额">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.money" placeholder="请输入内容" @change="handleEdit(scope.$index, scope.row)"></el-input>
            <span>{{scope.row.money}}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div>
      <p>服务时间:</p>
      <p>{{ detaildata.service_time }}</p>
    </div>
    <el-button type="info" @click="onSubmitPrint">打印</el-button>


  </div>
</template>

<script>
  export default {
    name: 'EnquiryDetail',
    data() {
      return {
        base_api_url: 'https://baojia.widesight.cn/backend/public',
        detaildata: [],
        item: [],
        country: '',
        city: '',
        address: '',
        zip: '',
        agentname: '',
        details_pieces: '',
        details_weight: '',
        details_volume: '',
        transportType: '',
        box: '',
        tableData: [{
          cost_breakdown: '1',
          currency: '',
          money: ''
        }, {
          cost_breakdown: '2',
          currency: '',
          money: ''
        }, {
          cost_breakdown: '3',
          currency: '',
          money: ''
        }]
      }
    },
    mounted: function() {
      this.detaildata = this.$route.params.data
      this.tableData = this.$route.params.item
      let tableData = this.tableData
      console.log(tableData)
    },
    methods: {
      handleEdit(index, row) {
        console.log(index, row);
      },
      handleCurrentChange(row, event, column) {
        console.log(row, event, column, event.currentTarget)
      },
      onSubmitPrint() {
        console.log( this.detaildata.country)
        this.$router.push({
          name: 'print',
          params: {
            country : this.detaildata.country,
            city:this.detaildata.city,
            address:this.detaildata.address,
            zip:this.detaildata.zip,
            agentname:this.detaildata.agentname,
            details_pieces:this.detaildata.details_pieces,
            details_fur:this.detaildata.details_fur,
            details_volume:this.detaildata.details_volume,
            transportType:this.detaildata.transportType,
            cost_breakdown:this.tableData.cost_breakdown,
            currency:this.tableData.currency,
            money:this.tableData.money,
            table_data: this.tableData
          }
        });
        // let country = this.detaildata.country
        // let city = this.detaildata.city
        // let address = this.detaildata.address
        // let zip = this.detaildata.zip
        // let agentname = this.detaildata.agentname
        // let details_pieces = this.detaildata.details_pieces
        // let details_fur = this.detaildata.details_fur
        // let details_volume = this.detaildata.details_volume
        // let transportType = this.detaildata.transportType
        // let cost_breakdown = this.tableData.cost_breakdown
        // let currency = this.tableData.currency
        // let money = this.tableData.money

      }
    }
  }
</script>

<style>
  .table3 {
    display: flex;
  }

  p.table3-name {
    width: 150px;
  }

  .enquirydetail input {
    height: 2rem;
    margin-top: 0.5rem;
    border: #ddd 1px solid;
  }

  input.data-unit {
    width: 60px;
    margin: 0.5rem 0.5rem 0 0.5rem;
  }

  input.address_type {
    width: 17.5rem;
  }

  .tb-edit .el-input {
    display: none
  }

  .tb-edit .current-row .el-input {
    display: block
  }

  .tb-edit .current-row .el-input+span {
    display: none
  }
</style>



